*{
    margin: 0px auto;
    padding: 0px auto;
    
  }
  html{
    height: 100%;
  }
  
  body{
  width:100%;
  height:100% ;
  background-image: url(/src/background-image/Chicago.jpg);
  background-size: cover;
  overflow: auto;
  }
  
 

  
  @media (max-width: 1024px){
  
    body {
        background-image: url(/src/background-image/Chicago2.jpg);
    }
        
  }
  
  @media (max-width: 768px){

   
  
    body{
      background-image: linear-gradient(10deg, #a5d9f4 0, #6cb4d8 50%, #1d90bd 100%);
      background-attachment: fixed;
      
      }
  }

  
  
  
  
 
 
 
  
