
h1
{
  font-size: 20px;
  margin-left: 20px;
  margin-top: 20px;
  font-family: 'Days One', sans-serif ;
  font-weight: lighter;
  text-shadow: 2px 4px 2px rgba(0,0,0,0.6);
  color:rgb(216, 211, 211);
  
}
h1 i{
  font-size: 20px;
  margin-left: 5px;
  color:rgb(216, 211, 211);
 
}

/*    FORM     */
.div-container
{
  display: flex;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10px;
  width: auto;
  height:60px;
  justify-content: center;
}
.form
{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  margin: 0px auto;
  height: 50px;
 
}
.contain-input
{
  border: 1px solid #E7E7EB;
  height: 38px;
  border-radius: 5px;
  background-color: rgb(39, 39, 39);
  opacity: 0.6;
}


#nameCity
{
  background-color: transparent;
  outline: none;
  border: none;
  line-height: 38px;
  color: snow;
  font-size: 15px;
}
#nameCity::placeholder
{ color: white;
  font-size: 14px;
}

#icono
{
  color:  #78797a;
  margin-left: 5px;
}

#button-search
{
  width: 70px;
  height: 38px;
  padding: 6px;
  font-size: 14px;
  margin-left: 10px;
  background-color: #50b2e4;
  border: green;
  border-radius: 5px;
  color:snow;
  cursor:pointer;
}

#button-search:hover
{
  background: rgb(45, 188, 245);
}

/*  componente infoweather          */


#today
{
  display: none;
  font-family: "roboto";
  text-align: center;
  margin-bottom: 30px;
  color: rgb(0, 0, 0);
  width: 80%;
  height: auto;
  background-image: linear-gradient(10deg, #a5d9f4 0, #6cb4d8 50%, #1d90bd 100%);
  justify-content: center;
  border: 1px solid #E7E7EB;
  border-radius: 5px;
  opacity:0.9;
  
}

@keyframes fadein{
  0%{opacity: 0 ;}
  100%{opacity:0.9;}
}
.btn-grados
{
  width:20%;
  float:right;
  margin-top: 20px;
}
.celsius
{
  width: 50%;
  float: right;
  background:transparent;
  border: none;
  cursor: pointer;
  margin-bottom: 5px;
  margin-left: 20px; 
  
}

.fahren
{
  float: right;
  width: 50%;
  background:transparent;
  border: none;
  cursor: pointer;
  margin: 0px;
  
}
.clearfix
{
  clear: both;
  float: none;
}
.temp
{
 font-size: 30px;
 font-family: Arial, Helvetica, sans-serif;
 font-weight: bold;
}

.temp-max,
.temp-min{
  width: 100px;
  
  margin-left: 10px;
  height: 30px;
  top:10%;
  text-align:center;
}
.description
{
  text-transform: capitalize;
  
  font-size: 20px;
}
.city
{
  font-size: 20px;
  font-weight: bold;
  margin-top: 20px;
}
.details
{
  display: grid;
  width: 90%;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 15px;
  font-family:"roboto";
  font-size: 14px;
  border-top: 1px solid rgb(54, 53, 53);
  
  padding-top: 15px;
}

.humidity{
  width: 100%;
  
  text-align: end;
}

.windspeed
{
  width: 100%;
  
  text-align: start;
}
.pressure
{
  width: 100%;
  text-align: end;
}

.feelslike
{
  width: 100%;
  text-align: left;
}

#location-icon
{
  width: 100%;
  margin-right: 10px;
}

.week{
  display:grid;
  grid-template-columns: repeat(4, 1fr);
  border-top: 1px solid rgb(53, 53, 53);
  width: 100%;
  height:250px;
}


/*      SOCIAL MEDIA ICONS     */
.social
{
  position: fixed;
  
  top:30%;
  z-index: 2000;
  
  }

.social a
{
  display:flex;
  align-items: center;
  text-align: center;
  font-size: 20px;
  transition: all 300ms ease;
  width: 50px;
  height: 50px;
  line-height: 50px;
  text-decoration: none;
  color: white;
  transition: all 300ms ease;
  cursor: pointer;
 
}
#face
{
  background-color: RGB(72,103,170);
  
}
#face:hover{
  background-color: rgb(20, 20, 20);
}


#tw{
  background: RGB(29,161,242);
  
}

#tw:hover{
  background-color: rgb(20, 20, 20);
}
#insta
{
  background: linear-gradient(45deg,#405de6,#5851db,#833ab4,#c13584,#e1306c,#fd1d1d);
  
}
#insta:hover
{
  background: rgb(20, 20, 20);
}
#email{
  background-color: rgb(87, 85, 85);
  
}
#email:hover {
  background-color: rgb(20, 20, 20);
}




/*map*/

#map{
  width: 70%;
  height:200px;
  margin-top: 10px;
  margin-bottom: 20px;
  border-radius: 5px;
  border: #78797a 1px solid;
}

/*             */

@media(max-width:768px){
  #today{
    background-image: none;
    border: none;
    height: 100vh;
  }
  body{
    height: min-content;
  }
}

@media (max-width:425px){
  .div-container{
    margin-top: 15px;
    height: 60px;
  }
  #today{
    border: none;
    width: 100%;
    
    background-image: none;
    
  }
  #map{
    width: 90%;
  }
 .social{
   background: rgba(0, 0, 0, 0.5);
   border-top-right-radius: 4px;
   border-bottom-right-radius: 4px;
   width: 45px;
   height: 180px;
 }
 
.social a{
  width: 30px;
  height: 30px;
  margin-top: 8px;
  line-height: 30px;
  border-radius: 3px;
}
 #face{
  margin-top: 20px;
}
 
  
    

  .form{
    height: 60px;
  }
  .details{
    width: 80%;
  }
  .btn-grados{
    margin-right: 10px;
  }
  
}
